<template>
    <div class="k-input">
        <!--  :value  @input  -->
        <!-- v-bind="$attrs" $attrs -->
        <input type="text" :value="value" @input="onInput" v-bind="$attrs" />
    </div>
</template>

<script>
    import emitter from "@/mixins/emitter";
    export default {
        inheritAttrs:false,// false
        mixins:[emitter],
        props:{
            value:{
                type:String,
                default:''
            },
            type:{
                type:String,
                default:'text'
            }
        },
        methods: {
            onInput(e) {
                //input
                this.$emit('input',e.target.value);

                // 
                // this.$parent.$emit('validate');
                
                // parent
                this.dispatch("KFormItem","validate")
            }
        },
    }
</script>

