<template>
    <div class="k-form">
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name:"KForm",
        componentName:"KForm",
        provide(){
            return {
                form:this
            }
        },
        props:{
            model:{
                type:Object,
                required:true
            },
            rules:{
                type:Object
            }
        },
        created(){
            this.field = [];
            this.$on('sunlau.form.addField',item => {
                this.field.push(item);
            })
        },
        methods:{
            validate(cb){
                // KFormItem
                //  [resultPromise]
                // const tasks = this.$children
                //   .filter(item => item.prop) // propItem
                //   .map(item => item.validate());

                // dispatch$children(propKFormItem)
                const tasks = this.field.map(item => item.validate());

                // Promise
                Promise.all(tasks)
                 .then(() => cb(true))
                 .catch(() => cb(false));
            }
        }
    }
</script>

