import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLogin:false
  },
  // statemutation
  mutations: {
    login(state){
      state.isLogin = true;
    },
    logout(state){
      state.isLogin = false;
    }
  },
  actions: {
    // 1vuexcontext{commit,dispatch,state}
    login({commit},username){
      // api1sadmin
      return new Promise((resolve,reject) => {
        setTimeout(() => {
          if(username === 'admin'){
            commit('login');
            resolve();
          }else{
            reject();
          }
        },1000)
      })
    }
  },
  modules: {}
});
