<template>
    <div>
        <KForm :model="userInfo" :rules="rules" ref="loginForm">
            <!--  -->
            <KFormItem label="" prop="username">
                <kInput v-model="userInfo.username" placeholder=""></kInput>
            </KFormItem>
            <!--  -->
            <KFormItem label="" prop="password">
                <kInput type="password" v-model="userInfo.password" placeholder=""></kInput>
            </KFormItem>
            <!--  -->
            <KFormItem>
                 <button @click="login"></button>
            </KFormItem>
            <!--  -->
            <KFormItem>
                 <button @click="register"></button>
            </KFormItem>
        </KForm>
    </div>
</template>

<script>
    import kInput from '@/components/form/KInput.vue'
    import KFormItem from '@/components/form/KFormItem.vue'
    import KForm from '@/components/form/KForm.vue'
    import Notice from '@/components/Notice.vue'
    export default {
        components: {
            kInput,
            KFormItem,
            KForm
        },
        data() {
            return {
                userInfo: {
                    username: "tom",
                    password: ""
                },
                rules: {
                    username: [{ required: true, message: "" }],
                    password: [{ required: true, message: "" }]
                }
            }
        },
        methods: {
            login() {
                this.$refs["loginForm"].validate(valid => {
                    const notice = this.$create(Notice, {
                    title: "",
                    message: valid ? "!" : "!",
                    duration: 2000
                    });
                    notice.show();
                
                    // if (valid) {
                    //   alert("submit");
                    // } else {
                    //   console.log("error submit!");
                    //   return false;
                    // }
                });
            },
            register(){
                this.$refs["loginForm"].validate(valid => {
                    if (valid) {
                      alert("register");
                    } else {
                      console.log("error register!");
                      return false;
                    }
                })
            }
        }
    }
</script>

